import { useMemo } from "react";
import { useHistory } from "@faharmony/router";
import { IBreadcrumbsProps } from "@faharmony/components";

import { useModule, ModuleProvider } from "./context";
import { IModuleProps, ISubModuleProps } from "./types";

/**
 * Hook to create a breadcrumbs to go back to parent Module.
 */
const useModuleBreadcrumbs = (): IBreadcrumbsProps["crumbs"] => {
  const { id: ModuleId, label, icon, t } = useModule();
  const history = useHistory();
  return useMemo(
    () => [
      {
        id: ModuleId,
        label: t("moduleName") || label || "",
        icon,
        onClick: () => history.push(`/${ModuleId}`),
      },
    ],
    [history, ModuleId, t]
  );
};

export { ModuleProvider, useModule, IModuleProps, ISubModuleProps };
export { useModuleBreadcrumbs };
