import React from "react";
import { IGlobalBarProps } from "./types";
import useAppSwitcher from "./apps";
import useGlobalActions from "./actions";
import { useValidKeycloak } from "./keycloak";
import FABar from "./FABar";

export const GlobalBar = (props: IGlobalBarProps) => {
  const { devMode = false, _harmony = false } = props;
  const { isAdmin, accountUrl, clients, logout } = useValidKeycloak(props);

  //   Hooks
  const apps = useAppSwitcher(clients, isAdmin, devMode, _harmony);
  const actions = useGlobalActions({ ...props, accountUrl, logout });

  return <FABar apps={apps} actions={actions} />;
};
